'use client';

import React, { useState } from 'react';
import { useSession } from 'next-auth/react';
import { Button } from '@/components/ui/button';
import { Dialog, DialogContent, DialogHeader, DialogTitle } from '@/components/ui/dialog';
import CreateOrEditUserForm from '@/app/(admin)/admin/dashboard/createUserForm';

export default function SettingsSection({
  // kept for backwards compatibility; not required for modal flow
  currentUserId,
  onOpenEdit,
}: {
  currentUserId?: string | number | null;
  onOpenEdit?: (payload: {
    id?: string | number | null;
    forceChangePassword?: boolean;
    forceChangeUsername?: boolean;
  }) => void;
}) {
  const { data: session, status } = useSession();
  const sessionUser = (session?.user as any) ?? null;
  const sessionUserId = sessionUser?.id ?? null;

  // Prefer explicit prop if provided, otherwise fall back to session user id.
  const id = currentUserId ?? sessionUserId;

  const loading = status === 'loading';

  // Local modal state: Settings will render the edit form inside its own modal
  const [openEditModal, setOpenEditModal] = useState(false);
  const [forceChangePassword, setForceChangePassword] = useState(false);
  const [forceChangeUsername, setForceChangeUsername] = useState(false);

  // When the user clicks action in Settings, open the modal and render the form inside SettingsSection.
  // Do NOT notify parent/dashboard to switch to the Users section.
  function openLocalEdit(forPassword = false, forUsername = false) {
    // Backwards compatibility: inform parent if it wants to be notified, but do NOT rely on parent to open UI
    if (onOpenEdit) {
      try {
        onOpenEdit({ id, forceChangePassword: forPassword, forceChangeUsername: forUsername });
      } catch {
        // ignore parent errors
      }
    }

    setForceChangePassword(Boolean(forPassword));
    setForceChangeUsername(Boolean(forUsername));
    setOpenEditModal(true);
  }

  return (
    <div className="w-full max-w-4xl mx-auto p-6 bg-background rounded-xl shadow">
      <h2 className="font-semibold text-xl mb-3">Settings</h2>

      <div className="space-y-4">
        <div>
          <h3 className="font-medium">User Settings</h3>
          <p className="text-sm text-muted-foreground">
            Edit your profile or change your password. The form will open inside this Settings
            panel.
          </p>
        </div>

        <div className="mt-2">
          {loading ? (
            <div className="text-sm text-muted-foreground">Loading session…</div>
          ) : id ? (
            <div className="text-sm">
              Signed in as{' '}
              <strong className="mr-2">
                {sessionUser?.username ?? sessionUser?.name ?? 'You'}
              </strong>
              <span className="text-muted-foreground">({sessionUser?.email ?? 'no email'})</span>
            </div>
          ) : (
            <div className="text-sm text-muted-foreground">No signed-in user found.</div>
          )}
        </div>

        <div className="flex gap-2 mt-3">
          <Button onClick={() => openLocalEdit(false, false)} disabled={!id || loading}>
            Edit my profile
          </Button>

          <Button
            onClick={() => openLocalEdit(true, false)}
            disabled={!id || loading}
            variant="outline"
          >
            Change my password
          </Button>

          <Button
            onClick={() => openLocalEdit(false, true)}
            disabled={!id || loading}
            variant="outline"
          >
            Change my username
          </Button>
        </div>

        <div className="mt-4 text-sm text-muted-foreground">
          Note: this Settings section will open the existing Create/Edit User form inside a modal
          here, pre-configured to edit the currently signed-in user's account.
        </div>
      </div>

      {/* Local modal that renders the CreateOrEditUserForm in edit mode */}
      <Dialog open={openEditModal} onOpenChange={(val) => !val && setOpenEditModal(false)}>
        {/* DialogContent styling: cap height and make inner area scrollable */}
        <DialogContent className="max-w-3xl w-full max-h-[85vh] p-0 sm:rounded-lg overflow-hidden">
          <DialogHeader>
            <DialogTitle className="px-4 py-3 border-b">Edit profile</DialogTitle>
          </DialogHeader>

          {/* Scrollable content area */}
          <div className="p-4 overflow-y-auto max-h-[72vh]">
            {id ? (
              <CreateOrEditUserForm
                mode="edit"
                userId={String(id)}
                // No initialData passed — the form will fetch the user by id itself.
                forceChangePassword={forceChangePassword}
                forceChangeUsername={forceChangeUsername}
                onSuccess={() => {
                  setOpenEditModal(false);
                  setForceChangePassword(false);
                  setForceChangeUsername(false);
                }}
                onCancel={() => {
                  setOpenEditModal(false);
                  setForceChangePassword(false);
                  setForceChangeUsername(false);
                }}
              />
            ) : (
              <div className="text-sm text-red-600">No user id available for editing.</div>
            )}
          </div>
        </DialogContent>
      </Dialog>
    </div>
  );
}
